import React, { Component } from 'react';
import './Login.css';
import SocialLogin from './SocialLogin'
import { login } from '../../util/APIUtils';
import { Link, Redirect } from 'react-router-dom';
import LoginImage from '../../img/login.svg';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { Form, Input, Button, Checkbox, Typography, Row, Col, message, Divider } from 'antd';
import { ACCESS_TOKEN } from '../../constants';
const { Title } = Typography;
class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            email: '',
            password: ''
        };
        this.onFinish = this.onFinish.bind(this);
    }
    componentDidMount() {
        if (this.props.location.state && this.props.location.state.error) {
            setTimeout(() => {
                message.error(this.props.location.state.error);
                this.props.history.replace({
                    pathname: this.props.location.pathname,
                    state: {}
                });
            }, 100);
        }
    }

    onFinish(values) {
        const loginRequest = Object.assign({}, values);
        login(loginRequest)
            .then(response => {
                localStorage.setItem(ACCESS_TOKEN, response.accessToken);
                message.success("You're successfully logged in!");
                this.props.loadCurrentlyLoggedInUser();
            }).catch(error => {
                message.error((error && error.message) || 'Oops! Something went wrong. Please try again!');
            });
    };

    onFinishFailed(errorInfo) {
        console.log('Failed:', errorInfo);
    };

    render() {
        if (this.props.authenticated) {
            return <Redirect
                to={{
                    pathname: "/",
                    state: { from: this.props.location }
                }} />;
        }

        return (
            <Row justify="center">
                <Col xs={0} sm={0} md={12}><img src={LoginImage} width="100%" alt="login"></img></Col>
                <Col xs={20} sm={20} md={8}>
                    <div className="login-content">
                        <Title level={2}>Login</Title>
                        <Form
                            layout="vertical"
                            name="normal_login"
                            className="login-form"
                            initialValues={{ remember: true }}
                            onFinish={this.onFinish}
                        >
                            <Form.Item label="Email Address" name="email" rules={[{ required: true, message: 'Please input your email!' }]}>
                                <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="email" />
                            </Form.Item>
                            <Form.Item label="password" name="password" rules={[{ required: true, message: 'Please input your Password!' }]}>
                                <Input.Password prefix={<LockOutlined className="site-form-item-icon" />} placeholder="Password" />
                            </Form.Item>
                            <Form.Item>
                                <Form.Item name="remember" valuePropName="checked" noStyle>
                                    <Checkbox>Remember me</Checkbox>
                                </Form.Item>
                                <span className="right">
                                    <Link className="login-form-forgot" to="/forgot">Forgot password</Link>
                                </span>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" block htmlType="submit" className="login-form-button">Log in</Button>
                            </Form.Item>
                            <Divider>or</Divider>
                            <SocialLogin />
                        </Form>
                        <div className="signup-link">Don't have an account? <Link to="/signup">Sign up</Link></div>
                    </div>
                </Col>
            </Row>
        );
    }
}

export default Login
