let API_URL;
let APP_URL;

const hostname = window && window.location && window.location.hostname;

if (hostname === 'cforcode.github.io') {
  API_URL = 'https://nerchuko.herokuapp.com';
  APP_URL = 'https://cforcode.github.io';
} else {
  API_URL = process.env.REACT_APP_BACKEND_HOST || 'http://localhost:8080';
  APP_URL = 'http://localhost:3000';
}

export const API_ROOT_URL = `${API_URL}`;
export const APP_ROOT_URL = `${APP_URL}`;