import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import { Card, Row, Col, Layout } from 'antd';
const { Content } = Layout;
const { Meta } = Card;
export default class AdminDashboard extends Component {
    constructor() {
        super();
        this.state = {
            cards: [
                { name: "Languages", image: "https://image.flaticon.com/icons/svg/484/484531.svg", path: "languages" },
                { name: "Topics", image: "https://image.flaticon.com/icons/svg/2422/2422223.svg", path: "topics" },
            ],
        }
    }
    render() {
        return (
            <Content>
                <Row gutter={[24, 24]}>
                    {this.state.cards.map((item, index) => (
                        <Col key={index} className="gutter-row" xs={12} sm={8} md={4}>
                            <Card
                                hoverable
                                style={{ textAlign: "center" }}
                                cover={<Link to={`/admin/${item.path}`}><img alt={item.name} src={item.image} style={{ height: "100px", width: "auto", margin: "24px auto 0" }} /></Link>}
                            >
                                <Meta title={<Link to={`/admin/${item.path}`}>{item.name}</Link>} style={{ textAlign: "center" }} />
                            </Card>
                        </Col>
                    ))}
                </Row>
            </Content>
        )
    }
}