import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Layout, Menu, Avatar, Typography, Dropdown } from 'antd';
import { UserOutlined } from '@ant-design/icons';
import { CaretDownOutlined } from '@ant-design/icons';
const { Header } = Layout;
const { SubMenu } = Menu;
class AppHeader extends Component {

    render() {
        const menu = (
            <Menu>
                <Menu.Item key="1"><Link to="/admin">Admin Dashboard</Link></Menu.Item>
                <Menu.Item key="2"><Link to="/profile">Profile</Link></Menu.Item>
                <Menu.Item key="3"><span onClick={this.props.onLogout}>Logout</span></Menu.Item>
            </Menu>
        );
        return (
            <Header>

                <Menu mode="horizontal" defaultSelectedKeys={['0']}>
                    <Menu.Item key="0"><Link to="/">Home</Link></Menu.Item>
                    {!this.props.authenticated && <Menu.Item className="right" key="3"><Link to="/login">Login</Link></Menu.Item>}
                    {!this.props.authenticated && <Menu.Item className="right xs-hide" key="4"><Link to="/signup">Signup</Link></Menu.Item>}
                    {this.props.authenticated &&
                        <SubMenu className="right"
                            title={
                                <Dropdown overlay={menu} placement="bottomCenter">
                                    <a className="ant-dropdown-link" onClick={e => e.preventDefault()}>
                                        {this.props.loggedInUser.imageUrl && <Avatar src={this.props.loggedInUser.imageUrl} />}
                                        {!this.props.loggedInUser.imageUrl && <Avatar icon={<UserOutlined />} />}
                                        <span className="ml1"><CaretDownOutlined /></span>
                                    </a>
                                </Dropdown>
                            }
                        >

                        </SubMenu>
                    }
                </Menu>
            </Header>
        )
    }
}

export default AppHeader;