import React, { Component } from 'react';
import { GOOGLE_AUTH_URL, FACEBOOK_AUTH_URL, GITHUB_AUTH_URL } from '../../constants';
import { GoogleSquareFilled, FacebookFilled, GithubFilled } from '@ant-design/icons';
import { Button } from 'antd';
class SocialLogin extends Component {
    render() {
        return (
            <div className="social-login">
                 <Button shape="round" style={{'background': '#dc4e41', 'color': '#fff'}} icon={<GoogleSquareFilled />} onClick={()=> window.location.href = GOOGLE_AUTH_URL} block>Google</Button>
                 <Button shape="round" style={{'background': '#3b5998', 'color': '#fff'}} icon={<FacebookFilled />} onClick={()=> window.location.href = FACEBOOK_AUTH_URL} block>Facebook</Button>
                 <Button shape="round" style={{'background': '#00405d', 'color': '#fff'}} icon={<GithubFilled />} onClick={()=> window.location.href = GITHUB_AUTH_URL} block>GitHub</Button>
            </div>
        );
    }
}
export default SocialLogin