import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import axios from 'axios';
import { Row, Col, Card, List, Avatar, Breadcrumb, Modal, Button, Form, Input } from 'antd';
export default class ListTopics extends Component {
    constructor() {
        super();
        this.state = {
            topics: [],
            visible: false,
        }
    }
    componentDidMount() {
        const config = {
            headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
        };
        axios.get(`http://localhost:8080/topics`, config)
            .then(res => {
                const topics = res.data;
                this.setState({ topics });
            })
    }

    showModal = () => {
        this.setState({
          visible: true,
        });
      };
    
      handleOk = e => {
        console.log(e);
        this.setState({
          visible: false,
        });
      };
    
      handleCancel = e => {
        console.log(e);
        this.setState({
          visible: false,
        });
      };

    render() {
        const { topics } = this.state;
        // const [form] = Form.useForm();
        return (
            <>
            <Row justify="center">
                    <Col span={20}>
                        <Breadcrumb>
                            <Breadcrumb.Item>Home</Breadcrumb.Item>
                            <Breadcrumb.Item>
                            <Link to={`/admin`}>Admin</Link>
                            </Breadcrumb.Item>
                            <Breadcrumb.Item>Topics</Breadcrumb.Item>
                        </Breadcrumb>
                    </Col>
                    <Col span={20}>
                    <Card size="small" title="Topics List" 
                    extra={<Button type="primary" size="small" onClick={this.showModal}>Add</Button>} >
                    <List
                            size="small"
                            itemLayout="horizontal"
                            dataSource={topics}
                            renderItem={(item, index) => (
                                <List.Item>
                                    <List.Item.Meta
                                        avatar={<Avatar shape="square" src= {item.imageUrl}/>}
                                        title={item.name}
                                    />
                                </List.Item>
                            )}
                        />    </Card>
                        
                    </Col>
                </Row>
                <Modal
          title="Basic Modal"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
        >
    <div>
      <Form
        layout="vertical"
        // form={form}
        // onValuesChange={onFormLayoutChange}
      >
        <Form.Item label="Field A">
          <Input placeholder="input placeholder" />
        </Form.Item>
        <Form.Item label="Field B">
          <Input placeholder="input placeholder" />
        </Form.Item>
        <Form.Item>
          <Button type="primary">Submit</Button>
        </Form.Item>
      </Form>
    </div>
        </Modal>
            </>
        )
    }
}