import React from 'react';
import { Link } from 'react-router-dom'
import axios from 'axios';
import { Row, Col, Card, List, Avatar, Breadcrumb } from 'antd';

export default class ListLanguages extends React.Component {
    constructor() {
        super();
        this.state = {
            languages: [],
            fontColorList: ['#613400', '#254000', '#092b00', '#002766'],
            backgroundColorList: ['#fffbe6', '#f4ffb8', '#d9f7be', '#bae7ff']

        }
    }

    componentDidMount() {
        const config = {
            headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
        };
        axios.get(`http://localhost:8080/languages`, config)
            .then(res => {
                const languages = res.data;
                this.setState({ languages });
            })
    }

    render() {
        return (
            <>
                
                <Row justify="center">
                    <Col span={20}>
                        <Breadcrumb>
                            <Breadcrumb.Item>Home</Breadcrumb.Item>
                            <Breadcrumb.Item>
                            <Link to={`/admin`}>Admin</Link>
                            </Breadcrumb.Item>
                            <Breadcrumb.Item>Languages</Breadcrumb.Item>
                        </Breadcrumb>
                    </Col>
                    <Col span={20}>
                    <Card size="small" title="Languages" 
                    extra={<a href="add">Add</a>} >

                        <List
                            size="small"
                            itemLayout="horizontal"
                            dataSource={this.state.languages}
                            renderItem={(item, index) => (
                                <List.Item>
                                    <List.Item.Meta
                                        avatar={<Avatar style={{ color: this.state.fontColorList[index], backgroundColor: this.state.backgroundColorList[index] }}>{item.code}</Avatar>}
                                        title={<a href="https://ant.design">{item.name}</a>}
                                        description={item.nativeName}
                                    />
                                </List.Item>
                            )}
                        />
                        </Card>
                    </Col>
                </Row>
            </>

        )
    }
}