import React, { Component } from 'react';
import { Route, Switch, BrowserRouter as Router } from 'react-router-dom'
import AppHeader from '../common/AppHeader';
import Login from '../user/login/Login';
import Signup from '../user/login/Signup';
import Profile from '../user/login/Profile';
import OAuth2RedirectHandler from '../user/login/OAuth2RedirectHandler';
import Home from '../component/Home';
import Page404 from '../component/common/Page404';
import Category from '../component/Category';
import AdminDashboard from '../component/admin/AdminDashboard';
import ListLanguages from '../component/admin/ListLanguages';
import ListTopics from '../component/admin/ListTopics';
import LoadingIndicator from '../common/LoadingIndicator';
import { getCurrentUser } from '../util/APIUtils';
import { ACCESS_TOKEN } from '../constants';
import PrivateRoute from '../common/PrivateRoute';
import { Layout, message } from 'antd';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      authenticated: false,
      currentUser: null,
      loading: false
    }

    this.loadCurrentlyLoggedInUser = this.loadCurrentlyLoggedInUser.bind(this);
    this.handleLogout = this.handleLogout.bind(this);
  }

  loadCurrentlyLoggedInUser() {
    this.setState({
      loading: true
    });

    getCurrentUser()
      .then(response => {
        this.setState({
          currentUser: response,
          authenticated: true,
          loading: false
        });
      }).catch(error => {
        this.setState({
          loading: false
        });
      });
  }

  handleLogout() {
    localStorage.removeItem(ACCESS_TOKEN);
    this.setState({
      authenticated: false,
      currentUser: null
    });
    message.success('You have been successfully logged out');

  }

  componentDidMount() {
    this.loadCurrentlyLoggedInUser();
  }

  render() {
    if (this.state.loading) {
      return <LoadingIndicator />
    }

    return (
      <Router basename={process.env.PUBLIC_URL}>
        <Layout className="layout">
          <AppHeader authenticated={this.state.authenticated} loggedInUser={this.state.currentUser} onLogout={this.handleLogout} />
          <>
            <Switch>
              <Route exact path="/" component={Home}></Route>
              <PrivateRoute path="/profile" authenticated={this.state.authenticated} currentUser={this.state.currentUser}
                component={Profile}></PrivateRoute>
              <Route path="/login" render={(props) => <Login authenticated={this.state.authenticated} {...props} loadCurrentlyLoggedInUser={this.loadCurrentlyLoggedInUser} />}></Route>
              <Route path="/signup" render={(props) => <Signup authenticated={this.state.authenticated} {...props} />}></Route>
              <Route path="/oauth2/redirect" component={OAuth2RedirectHandler}></Route>
              <Route path="/home" component={Home}></Route>
              <Route path="/admin/languages" component={ListLanguages} />
              <Route path="/admin/topics" component={ListTopics} />
              <Route path="/admin" component={AdminDashboard} />
              <Route path="/topics/:name" component={Category} />
              <Route path="*" component={Page404}></Route>
            </Switch>
          </>
        </Layout>
      </Router >
    );
  }
}

export default App;