import React, { Component } from 'react';
import { Card } from 'antd';

const { Meta } = Card;

class Profile extends Component {
    constructor(props) {
        super(props);
        console.log(props);
    }
    render() {
        return (
            <Card
                hoverable
                style={{ width: 240 }}
                cover={<img src={this.props.currentUser.imageUrl} alt={this.props.currentUser.name} />}
            >
                <Meta title={this.props.currentUser.name} description={this.props.currentUser.email} />
            </Card>
        );
    }
}

export default Profile