import React, { Component } from 'react';
import { Link, Redirect } from 'react-router-dom'
import SocialLogin from './SocialLogin'
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { signup } from '../../util/APIUtils';
import { Form, Input, Button, Checkbox, Typography, Row, Col, message, Divider } from 'antd';
import LoginImage from '../../img/login.svg';
const { Title } = Typography;

class Signup extends Component {
    render() {
        if (this.props.authenticated) {
            return <Redirect
                to={{
                    pathname: "/",
                    state: { from: this.props.location }
                }} />;
        }

        return (
            <Row justify="center">
                <Col xs={0} sm={14}><img src={LoginImage} width="100%" alt="login"></img></Col>
                <Col xs={20} sm={6}>
                    <div className="login-content">
                        <Title level={2}>Signup</Title>
                        <SignupForm {...this.props} />
                        <div className="signup-link">Already have an account? <Link to="/login">Login</Link></div>
                    </div>
                </Col>
            </Row>
        );
    }
}

class SignupForm extends Component {
    constructor(props) {
        super(props);
        this.state = {}
        this.handleInputChange = this.handleInputChange.bind(this);
    }

    handleInputChange(event) {
        const target = event.target;
        const inputName = target.name;
        const inputValue = target.value;

        this.setState({
            [inputName]: inputValue
        });
    }

    onFinish(values) {
        const signUpRequest = Object.assign({}, values);

        signup(signUpRequest)
            .then(response => {
                message.success("You're successfully registered. Please login to continue");
                this.props.history.push("/login");
            }).catch(error => {
                message.error((error && error.message) || 'Oops Something went wrong. Please try again');
            });
    };

    onFinishFailed(errorInfo) {
        console.log('Failed:', errorInfo);
    };

    render() {
        return (
            <Form
                name="normal_login"
                layout="vertical"
                className="login-form"
                initialValues={{ remember: true }}
                onFinish={this.onFinish}
            >
                <Form.Item label="Name" name="name" rules={[{ required: true, message: 'Please input your Name' }]}>
                    <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="name" />
                </Form.Item>
                <Form.Item label="Email Address" name="email" rules={[{ required: true, message: 'Please input your Email Address' }]}>
                    <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="email" />
                </Form.Item>
                <Form.Item label="Password" name="password" rules={[{ required: true, message: 'Please input your Password' }]}>
                    <Input.Password prefix={<LockOutlined className="site-form-item-icon" />} placeholder="Password" />
                </Form.Item>
                <Form.Item label="Confirm Password" name="confirmPassword" rules={[{ required: true, message: 'Please input your confirm Password' }]}>
                    <Input.Password prefix={<LockOutlined className="site-form-item-icon" />} placeholder="Confirm Password" />
                </Form.Item>
                <Form.Item>
                    <Form.Item name="remember" valuePropName="checked" noStyle>
                        <Checkbox>Remember me</Checkbox>
                    </Form.Item>
                    <span className="right">
                        <Link className="login-form-forgot" to="/forgot">Forgot password</Link>
                    </span>
                </Form.Item>
                <Form.Item>
                    <Button type="primary" block htmlType="submit" className="login-form-button">Sign up</Button>
                </Form.Item>
                <Divider>or</Divider>
                <SocialLogin />
            </Form>
        );
    }
}

export default Signup