import React from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { Card, Layout, Row, Col, List, Avatar, Skeleton } from 'antd';
import { API_ROOT_URL } from '../api-config';

const { Content } = Layout;
const { Meta } = Card;
export default class Home extends React.Component {
    constructor() {
        super();
        this.state = {
            cards: [1, 2, 3, 4, 5, 6],
            loading : true
        }
    }

    componentDidMount() {
        const config = {
            headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
        };
        axios.get(`${API_ROOT_URL}/topics`, config)
            .then(res => {
                const cards = res.data;
                this.setState({ cards, loading: false });
            })
    }

    render() {
        const { cards, loading } = this.state;
        return (
            <Content>
                <Row gutter={[24, 24]}>
                    {cards.map((item, index) => (
                        <Col key={index} className="gutter-row xs-hide" sm={8} md={6} lg={4}>
                            <Link to={`/topics/${item.name}`}>
                                <Card
                                    hoverable
                                    style={{ textAlign: "center" }}
                                    cover={<img alt={item.name} src={item.imageUrl} style={{ height: "100px", width: "auto", margin: "24px auto 0" }} />}
                                >
                                    <Meta title={item.name} style={{ textAlign: "center" }} />
                                </Card>
                            </Link>
                        </Col>
                    ))}
                </Row>
                <List className="sm-hide md-hide lg-hide"
                    itemLayout="horizontal"
                    dataSource={cards}
                    renderItem={item => (
                        <List.Item>                            
                            <Skeleton loading={loading} active avatar shape="square" paragraph={{ rows: 1 }} >
                            <Link to={`/topics/${item.name}`} style={{ 'width': '100%' }}>
                                <List.Item.Meta className="ml2"
                                    avatar={<Avatar shape="square" src={item.imageUrl} />}
                                    title={item.name}
                                    description={item.name}
                                />
                            </Link>
                            </Skeleton>
                        </List.Item>
                    )}
                />
            </Content>
        )
    }
}